import React, { createContext, useContext, useMemo } from 'react';
import { useLocalStorage } from '../hooks/useLocalStorage';

// --- Types ---

export interface UserProfile {
  labsCompleted: number;
  wordsMastered: number;
  streak: number;
  lastPlayed: string | null; // ISO Date string
  joinedDate: string;
  voiceURI?: string; // Preferred voice ID
}

interface UserContextType {
  user: UserProfile;
  updateProgress: (data: Partial<UserProfile>) => void;
  incrementStreak: () => void;
  clearData: () => void;
}

// --- Defaults ---

const defaultProfile: UserProfile = {
  labsCompleted: 0,
  wordsMastered: 0,
  streak: 0,
  lastPlayed: null,
  joinedDate: new Date().toISOString(),
  voiceURI: undefined,
};

const UserContext = createContext<UserContextType | undefined>(undefined);

// --- Provider ---

export const UserProvider: React.FC<{ children: React.ReactNode }> = ({
  children,
}) => {
  const [user, setUser] = useLocalStorage<UserProfile>(
    `spelling_bee_user`,
    defaultProfile,
  );

  const updateProgress = React.useCallback(
    (data: Partial<UserProfile>) => {
      setUser((prev) => ({ ...prev, ...data }));
    },
    [setUser],
  );

  const incrementStreak = React.useCallback(() => {
    setUser((prev) => {
      const today = new Date().toISOString().split(`T`)[0];
      const lastPlayed = prev.lastPlayed ? prev.lastPlayed.split(`T`)[0] : null;

      if (lastPlayed === today) {
        return prev;
      }

      let newStreak = 1;
      if (lastPlayed) {
        const yesterday = new Date();
        yesterday.setDate(yesterday.getDate() - 1);
        const yesterdayStr = yesterday.toISOString().split(`T`)[0];

        if (lastPlayed === yesterdayStr) {
          newStreak = prev.streak + 1;
        }
      }

      return {
        ...prev,
        streak: newStreak,
        lastPlayed: new Date().toISOString(),
      };
    });
  }, [setUser]);

  const clearData = React.useCallback(() => {
    setUser(defaultProfile);
  }, [setUser]);

  const value = useMemo(
    () => ({
      user,
      updateProgress,
      incrementStreak,
      clearData,
    }),
    [user, updateProgress, incrementStreak, clearData],
  );

  return <UserContext.Provider value={value}>{children}</UserContext.Provider>;
};

export const useUser = () => {
  const context = useContext(UserContext);
  if (!context) {
    throw new Error(`useUser must be used within a UserProvider`);
  }
  return context;
};
